@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&family=Roboto&family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;  
  overflow: visible;
}


body{
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.3em;
  line-height: 1.7;
}

:root {
  --tp-dark-blue: rgb(13, 6, 33);
  --tp-medium-blue: rgb(14, 8, 66);
  --tp-light-blue:rgb(25, 17, 101);
  --tp-accent-blue: #3fab9e;
  --tp-accent-hover: #65bcb1;
  --tp-accent-dark: #18937C;
  --tp-light-grey: #FDFDFD;
  --tp-grey:#d2d2d2;
  --base-shadow: 0 4px 23px 0px rgba(0, 0, 0, 0.3);
  --blue-shadow-from: #3fab9e;
  --blue-shadow-to: #1f554f;
  --blue-box-shadow:  0 14px 26px -12px var(--blue-shadow-from), var(--base-shadow), 0 10px 10px -5px var(--blue-shadow-to);
}

/* Common Components */

.visible{
  -webkit-animation: fadeInUp 1s;
          animation: fadeInUp 1s;
}
.spacer-link{
  height: 75px;
}
button{
  font-size: 0.8em;
}
.button{
  border: none;
  background-color: #3fab9e;
  background-color: var(--tp-accent-blue);
  padding: 0.5em 1.3em;
  border-radius: 5px;
  font-family: 'Source Sans Pro', serif;
  font-weight: bolder;
  color: #f0f0f0;
  font-size: 1em;
  box-shadow: 0 10px 10px -5px #1f554f;
  box-shadow: 0 10px 10px -5px var(--blue-shadow-to);
}
.button1{
  border: none;
  background-color: #3fab9e;
  background-color: var(--tp-accent-blue);
  padding: 0.5em 1.3em;
  border-radius: 5px;
  font-family: 'Source Sans Pro', serif;
  font-weight: bolder;
  color: #f0f0f0;
  /* box-shadow: 0 10px 10px -5px var(--blue-shadow-to); */
  transition: background-color 1s ease;
}

.button1-dark{
  border: none;
  background-color: rgb(14, 8, 66);
  background-color: var(--tp-medium-blue);
  padding: 0.5em 1.3em;
  border-radius: 5px;
  font-family: 'Source Sans Pro', serif;
  font-weight: bolder;
  color: #f0f0f0;
  /* box-shadow: 0 10px 10px -5px var(--blue-shadow-to); */
  transition: background-color 1s ease;
}

.button1:hover{
  background-color: #18937C;
  background-color: var(--tp-accent-dark);

}

.button1-dark:hover{
  background-color: rgb(25, 17, 101);
  background-color: var(--tp-light-blue);

}

.button2{
  border: none;
  background-color: rgb(13, 6, 33);
  background-color: var(--tp-dark-blue);
  padding: 0.5em 1.3em;
  border-radius: 5px;
  font-family: 'Source Sans Pro', serif;
  font-weight: bolder;
  color: white;
  font-size: 1.1em;
  box-shadow: 0 10px 10px -5px #1f554f;
  box-shadow: 0 10px 10px -5px var(--blue-shadow-to);
}

.icon-button{
  border: none;
  background-color: rgb(14, 8, 66);
  background-color: var(--tp-medium-blue);
  padding: 0.8em 0;
  border-radius: 5px;
  font-family: 'Source Sans Pro', serif;
  font-weight: bolder;
  color: #f0f0f0;
  box-shadow: 0 15px 15px -5px rgb(13, 6, 33);
  box-shadow: 0 15px 15px -5px var(--tp-dark-blue);
  width: 200px;
}

.button3{
  border: none;
  background-color: transparent;
  padding: 0.2em;
  border-radius: 5px;
  font-family: 'Source Sans Pro', serif;
  color: #3fab9e;
  color: var(--tp-accent-blue);
  font-size: 0.9em;
  font-weight: bolder;
}


.button4{
  border: 1px solid #65bcb1;
  border: 1px solid var(--tp-accent-hover);
  background-color: transparent;
  padding: 0.3em 2em;
  font-family: 'Source Sans Pro', serif;
  color: #65bcb1;
  color: var(--tp-accent-hover);
  font-size: 0.8em;
  margin: 2em 0;
  border-radius: 4px;
  transition: all 1s ease;
}

.button4:hover{
  background-color: #18937C;
  background-color: var(--tp-accent-dark);
  color: white;
}


.subheading{
  color: rgb(14, 8, 66);
  color: var(--tp-medium-blue);
  padding: 0.2em;

}
.subheading-2{
  font-family: 'Playfair Display', serif;
  color: #18937C;
  color: var(--tp-accent-dark);
  text-align: center;
}

h3{
  font-size: 1.3em;
}

h2{
  font-size: 2em;
}

a{
  text-decoration: none;
}

/* Nav Styling */
.App-logo {
  width: 8%;
  max-width:250px;
  min-width:175px;
}

.logo-small{
  width: 4%;
  max-width:75px;
  min-width:50px;
}
.logo-dark{
  width: 4%;
  max-width:75px;
  min-width:50px;
}
nav{
  background-color: rgb(13, 6, 33);
  background-color: var(--tp-dark-blue);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #f0f0f0;
  padding: 0.5em;
  /* position: sticky;
  top: 0;
  z-index: 999; */
}

.nav-emphasis{
  font-weight: bold;
  color: #3fab9e;
  color: var(--tp-accent-blue);
}
.nav-emphasis-dark{
  font-weight: bold;
  color: #18937C;
  color: var(--tp-accent-dark);
}
.nav-emphasis-white{
  font-weight: bold;
  color: white;
}

nav a{
  color: white;
  font-family: 'Source Sans Pro', serif;
}

.nav-tel{
  margin-right:0.5em;
}

.nav-tel p{
  margin:0.3em;
  text-align: center;
}
.nav-tel a:first-child > p{
  font-size:1.1em;
  -webkit-animation: pulse 3s infinite;
          animation: pulse 3s infinite;
  text-align: center;
}

/* Nav Sub_menu */
.MuiAppBar-root{
  background-color: #3fab9e !important;
  background-color: var(--tp-accent-blue) !important;
  position: -webkit-sticky !important;
  position: sticky !important;
  top: 0;
  z-index: 999; 
  flex-direction: row !important;
  align-items: center !important;
  justify-content: space-between !important;
}

.MuiContainer-root{
  width: auto !important;
  margin: 0 !important;
}

.MuiAppBar-root a{
  color: white;
}
.MuiButton-root{
  margin: 0.5em 2em !important;
  font-size: 0.8em !important;
  padding: 0 !important;
  color: white !important;
}

.MuiTypography-root{
  margin: 0.8em 0 !important;
  font-family: 'Source Sans Pro', serif !important;
  color: #65bcb1 !important;
  color: var(--tp-accent-hover) !important;
  font-weight: 500 !important;
}

.MuiMenuItem-root{
  font-size: 0.7em !important;
}
.MuiPopover-paper, .MuiMenuItem-root{
  background-color: rgb(13, 6, 33) !important;
  background-color: var(--tp-dark-blue) !important;
}
.dynamic-call-show{
  display: block;
}
.dynamic-call-hide{
  display: none;
}

#dynamic-call{
  font-weight: bolder;
  -webkit-animation: lightSpeedInLeft 3s;
          animation: lightSpeedInLeft 3s;
  margin-right: 0.8em;

}

.contact-mobile-bar{
  display: none;
}



/* Header/Hero */
video{
  -webkit-filter: brightness(50%);
          filter: brightness(50%);  
  height:100vh;
  width:100%;
  object-fit: cover;
}

.video-overlay {
  position: absolute;
  border-radius: 25px;
  margin: 2em;
  z-index: 2;
}
.App-header {
  position: relative;
  overflow-x:hidden;
}



.App-header span{
  color: #3fab9e;
  color: var(--tp-accent-blue);
  font-size: 1em;
}
.App-header h1{
  font-size: 3em;
  color: #fff;
  -webkit-animation: fadeInUp 2s;
          animation: fadeInUp 2s;
}
.App-header p{
  font-family: 'Playfair Display', serif;
  color: #f0f0f0;
  font-size: 1.2em;
  margin-top: 0.5em;
  font-weight: 500;
  -webkit-animation-name: fadeInUp, reveal;
          animation-name: fadeInUp, reveal;
  visibility: hidden;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.App-header button{
  margin-top: 2em;
}

.video-overlay div:first-child{
  padding: 2em;
  border-left: 4px solid #3fab9e;
  border-left: 4px solid var(--tp-accent-blue);
}
.App-link {
  color: #61dafb;
}
.contact-desktop-span{
  -webkit-animation-name: fadeInUp, reveal;
          animation-name: fadeInUp, reveal;
  visibility: hidden;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

/* Main */
main{
  width: 100%;
  margin-top: -1em;
  background-color: white;
}

/* ==================MISSION SECTION ================== */
#mission-section{
  background-color: #FDFDFD;
  background-color: var(--tp-light-grey);
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 3em;
}
.slogan{
  font-size: 1em;
  color: rgb(14, 8, 66);
  color: var(--tp-medium-blue);
  font-family: 'Playfair Display', serif;
}
.mission-statement{
  display: flex;
  justify-content: center;
}
.mission-statement > div{
  width: 80%;
  padding: 2em;
  text-align: justify;
}
.mission-statement img{
  width: 30%;
}

.mission-statement button{
  display: block;
  margin: 3em auto;
}

#mission-section > h2{
  padding-top: 2em;
}

#mission-section p {
  margin: 0.5em 0;
}


/* ==================MISSION BANNER CONTACT SECTION ================== */

.free-estimate-bar{
  background:  linear-gradient(rgba(0,0,0, 0.6), rgba(0,0,0, 0.6)), url(/static/media/interior_beige.597047bb.jpeg);
  background-size: cover;
  background-position: top;
  padding: 1em;
  min-height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.free-estimate-bar > div{
  padding: 2em;
}
.free-estimate-bar h3{
  font-size: 2em;
  text-align: center;
  font-family: 'Source Sans Pro', serif;
  color: white;
  text-shadow: 3px 4px 7px rgba(0,0,0,0.8);
}

.free-estimate-emphasis{
  color: #3fab9e;
  color: var(--tp-accent-blue);
}

.free-estimate-bar h4{
  color: #FDFDFD;
  color: var(--tp-light-grey);
  font-size: 1em;
  margin: 3em 0;
  font-family: 'Playfair Display', serif;
  text-shadow: 2px 3px 6px rgba(0,0,0,0.8);
}
.free-estimate-bar > div{
  margin-right: 0.5em;
}

/* ==================FEATURE PREVIEW SECTION ================== */
.services-preview{
  display: flex;
  justify-content: space-between;
  margin: 4em 0;
}

.services-preview-inner{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  max-width: 500px;
  box-shadow: 0 4px 4px 0px rgb(0 0 0 / 10%), 0 0 4px 0 rgb(0 0 0 / 8%);
  transition: all 1s ease-in;
  padding-bottom: 1em;
}

.services-preview-inner:hover{
  box-shadow: 0 4px 4px 3px rgb(0 0 0 / 25%), 0 0 8px 0 rgb(0 0 0 / 23%);
}
.services-preview-inner-text{
  display: flex;
  flex-direction: column;
  min-height: 300px;
}

.services-preview-inner p{
  text-align: justify;
  padding: 1em;
  font-size: 0.8em;
}

.services-preview img{
  width:100%;

}

.img-hover-zoom {
  overflow: hidden;
  margin-bottom: 1em;
  width:100%;
}

.img-hover-zoom--blur img {
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.img-hover-zoom--blur:hover img {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.services-preview-inner h3{
  text-align: center; 
  color: rgb(13, 6, 33); 
  color: var(--tp-dark-blue);
  font-family: 'Playfair Display', serif;
  font-size: 1.1em;
}
/* ==================FEATURE SECTION ================== */
.feature{
  background: linear-gradient(rgba(13,6,33, 0.95), rgba(14,8,66,0.95)), url(/static/media/paintbg.f9f1a821.jpeg);
  background-size: cover;
  background-position: center;
  padding: 5em 4em;
  display: flex;
  flex-direction: column;
  align-items:  center;
}
.feature-inner{
  margin-top: 2em;
  width: 61.8%;
}
.feature-container{
  display: flex;
  align-items: flex-start;
  margin: 0.3em 0;
}

.feature-inner p{
  margin-bottom: 3em;
}

.feature-container img{
  width: 30px;
  margin-right: 1.5em;
  -webkit-filter: invert(72%) sepia(70%) saturate(240%) hue-rotate(123deg) brightness(83%) contrast(86%);
          filter: invert(72%) sepia(70%) saturate(240%) hue-rotate(123deg) brightness(83%) contrast(86%);
}

.feature h2{
  text-align: center;
  color: #FDFDFD;
  color: var(--tp-light-grey);
  font-family: 'Source Sans Pro', serif;
}
.feature h3{
  text-align: center;
  color: #3fab9e;
  color: var(--tp-accent-blue);
  font-family: 'Playfair Display', serif;
  font-size: 1.2em;
}

.feature h4{
  color: #FDFDFD;
  color: var(--tp-light-grey);
  margin: 1em 0;
}

.feature-container ~ p{
  padding: 0 3em;
  color: #FDFDFD;
  color: var(--tp-light-grey);
  font-size: 0.9em;
  text-align: justify;
}

/* ==================SERVICES SECTION ================== */

.services-top-bar{
  display: flex;
  align-items: center;
  flex-direction: column;
}
.services-top-bar > div{
  margin: 2em 0;
}
.services-top-bar div:first-child{
  display: flex;
}
.services-top-bar img{
  width: 75px;
  height: 75px;
}
.services-top-bar + h4{
  margin: 0.5em;
  text-align: center;
  font-size: 1.2em;
}
.services-top-bar + p {
  text-align: center;
  font-size: 1em !important;

}
.services-section h2{
  text-align: center;
}
.services-section h3{
  font-family: 'Playfair Display', serif;
  font-weight: bold;
  color: rgb(13, 6, 33);
  color: var(--tp-dark-blue);
  margin: 1.5em 0 1.5em 0;
}
.services-top-bar p{
  text-align: center;
  margin: 0.5em;
  color: rgb(13, 6, 33) !important;
  color: var(--tp-dark-blue) !important;
  font-family: 'Playfair Display', serif;
  font-size: 0.8em;
}

.services-overlay{
  display: flex;
  padding: 2em 0;
}
.services-overlay + h4{
  text-align: center;
  font-size: 1.2em;
}
.services-list-wide{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
}
.services-overlay img{
  width: 100%;
  height:100%;
}
.services-img{
  position: relative;
  display: flex;
  object-fit: initial;
  justify-content: center;
  align-items: center;
}
.services-img:after {
  content: '\A';
  position: absolute;
  width: 100%; height:100%;
  top:0; left:0;
  background:rgba(13,3,66,0.9);
  opacity: 0;
  transition: all 1s;
  -webkit-transition: all 1s;
}
.services-img:hover:after {
  opacity: 1;
} 
.services-img-overlay{
  position: absolute;
  display: none;
  color: white;
  z-index: 10;
}
.services-img:hover .services-img-overlay{
  display: flex;
  flex-direction: column;
}
.services-img-overlay button{
  visibility: hidden;
}
.services-img-overlay a{
  margin: 1em auto;
}
.services-img:hover .services-img-overlay h4{
  -webkit-animation: fadeInUp 1.5s;
          animation: fadeInUp 1.5s;
}
.services-img:hover .services-img-overlay button{
  -webkit-animation: fadeInUp, reveal;
          animation: fadeInUp, reveal;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.services-section p{
  color: rgb(13, 6, 33);
  color: var(--tp-dark-blue);
  font-size: 0.8em;
  padding:0.5em;
}

.services-sub-text{
  padding: 2em;
}

.services-sub-text h4{
  color: rgb(13, 6, 33);
  color: var(--tp-dark-blue);
  margin-top: 2em;
}
.services-sub-text h4 + p{
 margin: 1em 0;
}
.services-sub-text h5{
  margin: 0.5em 0;
}
.services-list-container + h5{
  text-align: center;
}
.services-sub-text h3{
  border-bottom: 2px solid #18937C;
  border-bottom: 2px solid var(--tp-accent-dark);
  display: inline;
  padding-bottom: 0.2em;
}
.services-list-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  margin: 0.5em 0 2em 0;
}
.services-list-container + p{
  text-align: center;
}
.services-list-container ~ a{
  display: block;
  margin: 1.5em 0;
  display: flex;
  justify-content: center;
}
.services-list-item{
  display: flex;
  margin: 0.8em 0;
}

.services-list-item p{
  font-family: 'Source Sans Pro', serif;
  font-weight: 500;
}
.services-list-item img{
  -webkit-filter: invert(44%) sepia(58%) saturate(589%) hue-rotate(119deg) brightness(92%) contrast(86%);
          filter: invert(44%) sepia(58%) saturate(589%) hue-rotate(119deg) brightness(92%) contrast(86%);
  width: 20px;  
  margin-right: 10px;
}

.commercial-section{
  margin-bottom: 5em;
  background-color: #FDFDFD;
  background-color: var(--tp-light-grey);
}

/* ==================PROCESS SECTIONS ================== */

