}


/* ==================CONTACT SECTION ================== */

.contact-section{
  background: linear-gradient(rgba(13,6,33, 0.85), rgba(14,8,66, 0.85)), url(/static/media/interior_bg.bfd06636.jpeg);
  background-size: cover;
  padding: 6em 3em;
  min-height:700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contact-section h3{
  color: #FDFDFD;
  color: var(--tp-light-grey);
  text-align: center;
  font-family: 'Playfair Display', serif;
  font-size: 1.5em;
}
.contact-section p{
 font-style: italic;
 color: #18937C;
