/* Animations */
@-webkit-keyframes reveal {
  to{
    visibility: visible;
  }
}
@keyframes reveal {
  to{
    visibility: visible;
  }
}
@-webkit-keyframes fade {
  from{
    opacity: 0;
  }
  to{
    opacity: 0.7;
  }
}
@keyframes fade {
  from{
    opacity: 0;
  }
  to{
    opacity: 0.7;
  }
}

